.skin-blue label {
  display: inline !important;
  max-width: 100%;
  /* margin-bottom: 5px; */
  /* font-weight: 700 !important; */
}

.skin-blue .radio>label {
  position: relative;
  padding-left: 25px;
  margin-right: 0px;
  color: #666666;
}

.skin-blue .checkbox>label {
  position: relative;
  cursor: pointer;
  margin-right: 0px;
  padding-left: 25px;
}

.inline {
  display: inline-block;
  vertical-align: middle;
}
